import React, { useEffect } from 'react';
import { Styled } from './styled';
import { Dialog } from './dialog/Dialog';
import { useQuery } from '../hooks';
import { Chat } from './chat/Chat';
import { RoutesEnum } from '../routes/RoutesEnum';
import { observer } from 'mobx-react-lite';
import useWebSocket from 'react-use-websocket';
import { wsUrl } from '../url';
import { wsActions } from '../wsreducer';

export const Messager = observer(() => {
  const userIdQuery = useQuery('userId');

  const { lastJsonMessage } = useWebSocket(`${wsUrl}/rooms`);

  useEffect(() => {
    if (lastJsonMessage) {
      wsActions(lastJsonMessage);
    }
  }, [lastJsonMessage]);

  return (
    <Styled.Messager>
      {!userIdQuery ? (
        <>
          <Styled.Link to={`${RoutesEnum.Messager}?room=123`}>
            <Dialog />
          </Styled.Link>
        </>
      ) : (
        <Chat userId={userIdQuery} />
      )}
    </Styled.Messager>
  );
});
